<template>
  <html>

  <body>
    <nav class="navbar fixed-top navbar-expand-sm navbar-light bg-light" aria-label="Volume Segmentation Toolkit">
      <div class="container-fluid">
        <div class="navbar-brand">Volume Segmentation Toolkit</div>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbar_top"
          aria-controls="navbar_top" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbar_top">
          <ul class="navbar-nav ms-auto">
            <li class="nav-item">
              <a class="nav-link active" aria-current="page" href="#">Help</a>
            </li>
          </ul>
        </div>
      </div>
      <hr />
    </nav>
    <div class="container-fluid mt-20">
      <div class="row gx-1">
        <div class="col-3">
          <div class="card">
            <div class="card-header">
              <div class="float-start">
                Tasks
              </div>
              <div class="float-end">
                <h3><i class="bi bi-plus-square-fill clickable" ref="new_task_button"
                    @click.self="position_task_menu()"></i>
                </h3>
              </div>
            </div>
            <ul class="list-group list-group-flush">
              <!-- Utility Card Header Here -->
              <li v-bind:class=get_task_class(card) v-for="card in utility_cards.value" :key="card.id"
                @click="select_card_by_id(card.id, 'utility')">* {{card.id}} * <div class="float-end"><i
                    class="bi bi-x-lg" @click="remove_card(card.id)"></i></div>
              </li>
              <!-- Tasks Headers here -->
              <li v-bind:class=get_task_class(task) v-for="task in tasks.value" :key="task.id"
                @click="select_card_by_id(task.id, 'task')">{{task.id}}</li>
            </ul>
          </div>
        </div>
        <div class="col-9">
          <!-- Tasks cards Here -->
          <KeepAlive>
            <TaskCard v-if="display_context=='task'" :is="selected_card" :id="selected_card.id"
              :struct="selected_card.struct" :active="selected_card.active" />
          </KeepAlive>
          <SettingsCard v-if="display_context=='utility'" :is="selected_card" :id="selected_card.id"
            :struct="selected_card.struct" :active="selected_card.active" />
        </div>
      </div>
      <NewTaskMenu ref="task_menu" @process_chosen="new_task" />
    </div>
  </body>

  </html>
</template>

<script setup>
import { ref, reactive, computed } from "vue";
import TaskCard from '@/components/TaskCard.vue';
import NewTaskMenu from '@/components/NewTaskMenu.vue';
import { useVSUIStore } from '@/stores/vsui_store.js';
import { useToast } from "vue-toastification";
import SettingsCard from "./components/SettingsCard.vue";
const store = useVSUIStore();
const socket = store.socket;
const toast = useToast();
const display_context = ref('task')
const new_task_button = ref(null)
const task_menu = ref(null)

const available_processes = reactive({
  value: []
})
const tasks = reactive({
  value: [{
    "id": "No Connection",
    "struct": [{ "key": "Nothing to Show", "component": "LogElement" }],
    "active": false
  }]
})
const utility_cards = reactive({
  value: []
})
// the index of the currently viewed card
const selected_card_index = ref(0)
// the currently selected card, found based on the selected index
const selected_card = computed(() => {
  if (display_context.value == 'task') {
    return (selected_card_index.value < tasks.value.length) ? tasks.value[selected_card_index.value] : tasks.value[0]
  } else if (display_context.value == 'utility') {
    return (selected_card_index.value < utility_cards.value.length) ? utility_cards.value[selected_card_index.value] : tasks.value[0]
  } else {
    return tasks.value[0]
  }
})
// set the selected task to be the one with the chosen id
function select_card_by_id(target_id, card_type) {
  if (card_type == 'task') {
    selected_card_index.value = tasks.value.findIndex(x => x.id === target_id);
    display_context.value = 'task'
  } else if (card_type == 'utility') {
    selected_card_index.value = utility_cards.value.findIndex(x => x.id === target_id);
    display_context.value = 'utility'
  }
}
function remove_card(card_id) {
  let index = select_card_by_id(card_id)
  if (index > -1) {
    utility_cards.value.splice(index, 1);
  }
}
// FUNCTIONS
// get the class for the item in the tasks list based on: selection status; running state ect
function get_task_class(task) {
  // spaces must be added before appended class statements
  var class_string = "list-group-item pointer clickable"
  if (selected_card.value.id == task.id) {
    class_string += " border border-primary border-4"
  }
  if (task.active) {
    class_string += " active-task"
  } else {
    class_string += " inactive-task"
  }
  return class_string
}

function position_task_menu() {
  const left = new_task_button.value.getBoundingClientRect().left
  const bottom = new_task_button.value.getBoundingClientRect().bottom
  task_menu.value.set_position(left, bottom);
  task_menu.value.open(available_processes.value);
}

// SocketIO
// these events handle the connection between the frontend and backend
socket.on('connect', () => {
  console.log('connected')
  toast("connected");
})
socket.on('disconnect', () => {
  console.log('disconnected')
  toast.error('disconnected');
})
socket.on('tasks', (data) => {
  tasks.value = data.data;
})
socket.on('available_processes', (data) => {
  available_processes.value = data.data;
  console.log(available_processes.value)
})
// pass notifications to toasts
socket.on('toast', (data) => {
  var txt = data.txt;
  var type = data.type;
  if (type == 'success') {
    toast.success(txt);
  } else if (type == 'error') {
    toast.error(txt);
  } else if (type == 'info') {
    toast.info(txt);
  } else if (type == 'warning') {
    toast.warning(txt);
  }
})
function new_task(option) {
  console.log(option.name)
  if (!utility_cards.value.some(x => x.id === option.name)) {
    utility_cards.value.push({ "id": option.name, "struct": option.settings_schema, "active": true })
  }
  select_card_by_id(option.name, 'utility')
}
</script>

