import {defineStore} from 'pinia';
import {reactive} from 'vue';
import io from 'socket.io-client';
//import { useToast } from "vue-toastification";

export const useVSUIStore = defineStore('vsui_store', () => {
  const socket = io({
    extraHeaders: {
      'type': 'web'
    }
  })
  const available_processes = reactive({value : []})
  function set_available_processes(data) {
    available_processes.value = data
    console.log(available_processes.value)
  }
  return { socket, set_available_processes}
})