<template>
    <div class="foggy" @click.stop="close()" v-if="opened">
        <div class="justify-content-center">
            <div class="row">
                <div class="col" v-for="image in images" :key="image">
                    <img :src="image" class="img-fluid" />
                </div>
            </div>
        </div>
    </div>
</template>
<script setup>
import { ref, defineExpose } from 'vue';
const opened = ref(false)
const images = ref([])

// eslint-disable-next-line
function set_images(x) {
    images.value = x
}
function close() {
    opened.value = false
}
function open() {
    opened.value = true
}
// eslint-disable-next-line
function add_to_comparison() {
    images.value.push(images.value[0])
}
defineExpose({ open, close, set_images })
</script>
<style scoped>
.foggy {
    position: fixed;
    background-color: rgba(50, 50, 50, 0.7);
    height: 100vh;
    width: 100vw;
    z-index: 200;
    top: 0;
    left: 0;
}

.image_row {
    height: 40vh;
}

.image_carousel {
    height: 30vh;
}
</style>